import Vue from 'vue'
import VButton from './VButton'
import Datepicker from 'vuejs-datepicker'
import { en } from 'vuejs-datepicker/dist/locale'
import BLoading from './BLoading'
import TextareaAutosize from './TextareaAutosize'
import DateTimePicker from './datetimepicker/Index'
import ImageCropper from './cropper'

let km = {
  dow: 7, // Sunday is the first day of the week
  hourTip: '', // tip of select hour
  minuteTip: '', // tip of select minute
  secondTip: '', // tip of select second
  yearSuffix: '', // suffix of head
  monthsHead: '___________'.split('_'), // months of head
  months: '___________'.split('_'), // months of panel
  weeks: '______'.split('_'), // weeks,
  cancelTip: '', // text for cancel button for daterange picker
  submitTip: '' // text for submit button for daterange picker
};

// Components that are registered globally.

[
  VButton,
  BLoading,
  TextareaAutosize,
  ImageCropper
].forEach(Component => {
  Vue.component(Component.name, Component)
})

Vue.component('date-time-picker', {
  extends: DateTimePicker,
  props: {
    local: {
      type: Object,
      default: () => km
    }
  }
})

Vue.component('b-datepicker', {
  extends: Datepicker,
  props: {
    bootstrapStyling: {
      type: Boolean,
      default: true
    },
    language: {
      type: Object,
      default: () => en
    }
  }
})
